@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');

*{
 font-family: "Roboto Condensed",sans-serif;
}
body{
    margin:0;
    padding: 0;
    background-color: #2f3640;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url(./image/background.png);
}
.container{
    width: 350px;
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0,0, 0.1);
}
.input-container{
    width: 100%;
    display: flex;
    align-items: center;
    border: 2px solid #70e6f3;
    border-radius: 10px;
    overflow: hidden;
}
.cityInput{
    flex:1;
    height:30px;
    border: none;
    font-size: 16px;
    outline: none;
    padding: 10px;
}
.search-icon{
    padding: 10px;
    height: 30px;
    cursor: pointer;
}
.search-icon img{
    object-fit: cover;
    width: 25px;
    height: 25px;
    margin-top: 5px;
}
.image{
    text-align: center;
    height: 150px;
    margin-bottom: 10px;
}
.image img{
    width: 160px;
    height: 160px;
}
.temp,.location,.country{
    margin-top: 5px;
    font-size: 35px;
    color: #333;
    text-transform: uppercase;
    text-align: center;
}
.temp{
    font-weight: 500;
}
.location{
    color: #ffbc00;
    font-weight: 400;
    font-size: 40px;
}
.country{
    font-size: 18px;
    color: #888;
    font-weight: 500;
}
.cord{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    margin-top: 10px;
    text-align: center;
}
.cord div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
}
.cord div span{
    font-size: 14px;
}
.humidity-percent,
.cord div span :nth-child(2){
    font-size: 18px;
    font-weight: bold;
    color: #665;
    padding-top: 5px;
}
.data-container{
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}
.element{
    text-align: center;
}
.icon{
    width: 40px;
    height: 40px;
}
.text{
    font-size: 12px;
    color: #888;
}
.copyright{
    text-align: center;
    font-size: 12px;
    color: #888;
}
.copyright span{
     color: #333;
}

.loading-message,
.error-message,
.city-not-found{
    margin-top: 10px;
    color: #888;
    font-size: 25px;
    font-weight: 400;
    text-align: center;
}